@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Homepage */
.home-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#gameTitle {
  font-weight: 400;
  font-size: 150px;
  border: 2px solid;
  border-radius: 10px;
  padding: 10px 6%;
  margin: 100px 60px 40px 60px;
  /* background-color: rgb(3, 182, 26); */
}

.home-page p {
  font-weight: 300;
  font-size: 50px;
  margin: 20px 0px;
}

#start-button {
  border-radius: 10px;
  border: 2px solid;
  cursor: pointer;
  margin: 20px 0px;
  width: 200px;
  font-size: 25px;
  transition: all 0.4s ease 0s;
}

#start-button:hover {
  background-color: rgb(69, 238, 229);
  transform: scale(1.1);
}
/*
  Game Menu
*/
.game-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.game-menu h2 {
  font-weight: 400;
  font-size: 60px;
  border: 2px solid;
  border-radius: 10px;
  padding: 10px 6%;
  margin: 100px 60px 30px 60px;
}

.game-menu ul {
  font-weight: 300;
  font-size: 30px;
  list-style: none;
  margin-right: 45px;
}

.game-menu ul li {
  border: 2px solid;
  border-radius: 10px;
  padding: 10px 6%;
  margin: 15px 0px;
}

.game-menu ul li:hover {
  background-color: rgb(233, 139, 17);
  color: white;
}
/*
    Settings
*/
#setting-title {
    font-weight: 400;
    font-size: 80px;
}


/*
    Quiz game
*/

#quiz-question {
    font-size: 25px;
}

#quiz-prompt {
    font-size: 28px;
}

.ans-btn {
    font-size: 30px;
    color: white;
    border: 1px solid;
    border-radius: 10px;
    margin: 30px;
    padding: 10px 20px;
}

.ans-btn:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.quiz-score {

}

.button {
    margin: 15px;
    border: 2px solid;
    padding: 6px 15px;
    border-radius: 10px;
    font-size: 20px;
    cursor: pointer;
}
/*
    Typing game
*/

.typing-game {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* text-align: center; */
}

.typing-game h1 {
    font-weight: 400;
    font-size: 50px;
    border: 2px solid;
    border-radius: 10px;
    padding: 10px 6%;
    margin: 100px 60px 30px 60px;
}

.typing-container {
    text-align: center;
    padding: 0 15px;
}

.Character {
    white-space: pre;
    font-size: 50px;
}

.Character-current {
    background-color: #00c0da;
    border-radius: 5px;
    color: white;
    padding: 5px;
    font-size: 50px;
}

.Character-out {
    color: rgb(141, 141, 141);
}

.typed-text-container {
    font-size: 25px;
}

.score {
    border: 2px solid;
    border-color: rgb(0, 153, 255);
    border-radius: 10px;
    background-color: rgb(0, 153, 255);
    padding: 10px 20px;
    color: white;
    /* width: 100px; */
}

.button {
    margin: 15px;
    border: 2px solid;
    padding: 6px 15px;
    border-radius: 10px;
    font-size: 20px;
    cursor: pointer;
}

.button:hover {
    background-color: black;
    color: white;
}

.buttons {
    display: flex;
    flex-direction: row;
}
* {
  font-family: Lato;
}

#page-body {
  padding: 0px 5%;
  text-align: center;
}

@media (max-width: 1100px) {
  #gameTitle {
    font-size: 100px;
  }
  .App p {
    font-size: 40px;
  }
}

@media (max-width: 700px) {
  #gameTitle {
    font-size: 80px;
  }
  .App p {
    font-size: 30px;
  }
}


